body {
  font-size: 1rem !important;
  padding-right: 0 !important;

  @include media-breakpoint-up(lg) {
    //         font-size: 1.0625rem !important;
    font-size: 1.05rem !important;
  }
}

h1,
h2,
h3,
h4 {
  br {
    display: none;
  }

  @include media-breakpoint-up(sm) {
    br {
      display: inline-block;
    }
  }
}

.btn {
  box-shadow: none;
}
.divider:not(:first-child) {
  top: 1px;
}

.offset-large-img {
  .divider {
    position: relative;
    margin-bottom: -6px;

    svg {
      background: none !important;
      height: 135px;
    }
  }

  path,
  polygon {
    fill: $gray-25;
  }
}

sup {
  position: relative;
  font-size: 40%;
  line-height: 0;
  vertical-align: baseline;
  top: -1.2em;
}

.offset-large-img {
  .row:last-of-type {
    margin-bottom: -$spacer * 15;
    z-index: 2;
    position: relative;
  }

  +section {
    padding-top: $spacer * 15;

    @include media-breakpoint-up(md) {
      padding-top: $spacer * 22;
    }
  }
}

.opacity-0 {
  opacity: 0;
}

.opacity-1 {
  opacity: 0.1;
}

.opacity-2 {
  opacity: 0.2;
}

.opacity-3 {
  opacity: 0.3;
}

.opacity-4 {
  opacity: 0.4;
}

.opacity-5 {
  opacity: 0.5;
}

.opacity-6 {
  opacity: 0.6;
}

.opacity-7 {
  opacity: 0.7 !important;
}

.opacity-8 {
  opacity: 0.8;
}

.opacity-9 {
  opacity: 0.9;
}

.opacity-100 {
  opacity: 1;
}

.mt-n1 {
  @include media-breakpoint-up(xl) {
    margin-top: -6.95rem !important;
  }
}

.mw-130 {
  position: relative;
  left: -13%;
  max-width: 160%;
  max-width: 160%;

  &.float-right {
    @include media-breakpoint-down(md) {
      left: 20%;
    }
  }

  @include media-breakpoint-up(md) {
    left: auto;
    max-width: 200%;
  }

  @include media-breakpoint-up(lg) {
    max-width: 180%;
  }

  @include media-breakpoint-up(xl) {
    max-width: 160%;
  }

  @include media-breakpoint-up(xxl) {
    margin-left: -40px;
    max-width: 150%;
  }
}

.offset-l {
  @include media-breakpoint-down(sm) {
    margin-left: -9.25rem !important;
  }
}

section {
  padding: 4.5rem 0;

  @include media-breakpoint-up(md) {
    padding: 4.5rem 0;
  }
}

.avatar {
  flex-shrink: 0;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.avatar-author {
  display: flex;
}

.avatar-author h6 {
  margin-bottom: 0;
}

p {
  line-height: 1.9;
}

.section-break {
  background: #f2f2f2;
  text-align: center;
  font-size: 11px;
  line-height: 30px;
}

html:not(.no-js) .smooth #main-scrollbar {
  @include media-breakpoint-up(sm) {
    position: relative;
    display: block;
    overflow: hidden;
    height: 100vh;
    outline: none;
  }
}

.l0 {
  letter-spacing: 0 !important;
}

main {
  overflow: visible !important;
}

.rounded-xl {
  border-radius: $border-radius-xl !important;
  overflow: hidden;
}

.rounded-md-xl {
  @include media-breakpoint-up(sm) {
    border-radius: $border-radius-xl !important;
    overflow: hidden;
  }
}

.pt-100 {
  padding-top: 100px;
}

.pb-100 {
  padding-bottom: 100px;
}

.section-title {
  margin-bottom: 60px;
}

.section-title p {
  color: #777;
  font-size: 16px;
}

.section-title h4 {
  position: relative;
  margin-bottom: 20px;
  padding-bottom: 20px;
  text-transform: capitalize;
  font-weight: 600;
  font-size: 40px;
}

.section-title h4:before {
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -30px;
  width: 60px;
  height: 2px;
  background-color: #ff3636;
  content: "";
}

.section-title h4:after {
  position: absolute;
  bottom: -4px;
  left: 50%;
  margin-left: -5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ff3636;
  content: "";
}

.single-team {
  position: relative;
  box-shadow: 0 8px 18px -12px rgba(36, 36, 81, .15);
  border-radius: 10px;

  img {
    display: inline-block;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    max-height: 300px;
  }
}

.team-hover {
  left: 0;
  padding: 20px;
  width: 100%;
  background: $white;
  text-align: center;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.team-hover h4 {
  text-transform: capitalize;
  font-size: 18px;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 280px;
}

.team-hover .subtitle {
  display: block;
  font-size: 15.5px;
  line-height: 1.4;
  opacity: .9;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 280px;
}

.team-hover h4 span {
  display: block;
  margin: 10px 0;
  font-weight: 300;
  font-size: 14px;
}

.team-hover a {
  display: inline-block;
  padding: 0 5px;
  color: $white;
}

.single-team:hover .team-hover {
  bottom: 0;
}

.single-team {
  overflow: hidden;
}

.container {
  position: relative;

  &.vw {
    @include media-breakpoint-down(sm) {
      padding-right: 0 !important;
      padding-left: 0 !important;
    }
  }

  &.full {
    padding-right: 20px;
    padding-left: 20px;

    @include media-breakpoint-up(md) {
      padding-right: 40px;
      padding-left: 40px;
    }

    @include media-breakpoint-up(xl) {
      padding-right: 60px;
      padding-left: 60px;
    }
  }

  &.features {
    @include media-breakpoint-up(md) {
      padding-bottom: 50px;
    }
  }
}

.features {
  p {
    margin-top: 1rem;
    color: lighten($body-color, 15%);
    letter-spacing: -0.3px;
    font-weight: 400;
    line-height: 1.75;

    @include media-breakpoint-up(md) {
      font-size: 18.5px;
    }

    &.small {
      margin-top: 7px;
      font-size: 16px;
      line-height: 1.2;
    }
  }

  h1 {
    letter-spacing: -1.1px;
    font-weight: 600;
  }
}

$solid-bg1: #018ef5;
$solid-bg2: $gray-900;
$solid-bg3: #060812;
$solid-bg4: #ff8d67;
$gradient-bg1: linear-gradient(180deg, $white 30%, #f7faff 100%);
$gradient-bg2: linear-gradient(-168deg, #64b5ff 0%, #002cff 100%);
$gradient-bg3: linear-gradient(-168deg, #46a7ff 0%, #1a1c27 100%);
$gradient-bg4: linear-gradient(-168deg, #ffad67 0%, #ee835f 100%);
$gradient-bg5: linear-gradient(-162deg, #83a9ff 0%, #000000 100%);
$gradient-bg6: linear-gradient(-166deg, #616bff 0%, #29c3f6 85%);
$background-solid: url(/img/bg-sub.png),
$solid-bg1;
$background-gradient: url(/img/bg-sub.png),
$gradient-bg2;

.header-bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: $background-gradient;
  background-position: -193px -33px, center center;
  background-position: -203px 37px, center center;
  background-size: 83%, 100%;
  background-size: 83%, 100%;
  background-repeat: no-repeat !important;
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000;
  // animation: fadein 500ms ease-in-out;

  &::before {
    position: absolute;
    right: 0;
    bottom: 2vw;
    z-index: 0;
    padding-bottom: 130px;
    min-width: 830px;
    width: 55vw;
    height: 100%;
    background: linear-gradient(165deg, rgba(66, 4, 230, 0) 15%, #4204e6 50%);
    background-position: 100% 23%;
    background-size: 100%;
    background-repeat: no-repeat;
    content: "";
    clip-path: polygon(-29% 0%, 100% 0, 100% 100%, 124% 100%);
    transform: translateZ(0);
    backface-visibility: hidden;
    perspective: 1000;
    opacity: 0.365;
    animation: fadein2 1000ms ease-in-out;
  }
}

.fade-out { animation: fadeout 300ms ease-in-out forwards !important; }
.fade-in { animation: fadein 300ms ease-in-out forwards; }

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes fadein2 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.365;
  }
}

.tag-lg {
  display: inline-block;
  margin-top: 0;
  margin-right: 10px;
  margin-bottom: 22px;
  padding: 0 9px;
  border-radius: 4px;
