@include media-breakpoint-up(sm) {
  html:not(.no-js) [data-aos^="basic"][data-aos^="basic"] {
    opacity: 0;
    transition-property: opacity, transform;
    transform: translateY(50px) !important;
  }

  html:not(.no-js) [data-aos^="basic"][data-aos^="basic"].aos-animate {
    opacity: 1;
    transform: translateY(0px) !important;
  }

  html:not(.no-js) [data-aos^="resize"][data-aos^="resize"] {
    transform: scale(1.2) !important;
    opacity: 0;
    transform-origin: top left;
    transition-property: opacity, transform;
  }

  html:not(.no-js) [data-aos^="resize"][data-aos^="resize"].aos-animate {
    opacity: 1;
    transform: none !important;
  }

  html:not(.no-js) [data-aos^="device_one"][data-aos^="device_one"] {
    top: -105px;
    opacity: 0;
    transition-property: top, opacity;
  }

  html:not(.no-js) [data-aos^="device_one"][data-aos^="device_one"].aos-animate {
    top: -155px;
    opacity: 1;
  }

  html:not(.no-js) [data-aos^="device_two"][data-aos^="device_two"] {
    top: 780px;
    opacity: 0;
    transition-property: top, opacity;
  }

  html:not(.no-js) [data-aos^="device_two"][data-aos^="device_two"].aos-animate {
    top: 730px;
    opacity: 1;
  }

  html:not(.no-js) [data-aos^="device_three"][data-aos^="device_three"] {
    top: 358px;
    opacity: 0;
    transition-property: top, opacity;
  }

  html:not(.no-js) [data-aos^="device_three"][data-aos^="device_three"].aos-animate {
    top: 308px;
    opacity: 1;
  }

  html:not(.no-js) [data-aos^="slide-in"][data-aos^="slide-in"] {
    opacity: 0;
    transition-property: opacity, transform;
    transform: translateX(-50px);
  }

  html:not(.no-js) [data-aos^="slide-in"][data-aos^="slide-in"].aos-animate {
    opacity: 1;
    transform: none;
  }
}

@media (min-width: 992px) {
  .anim {
    -webkit-animation-duration: 0.3s;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    -webkit-animation-fill-mode: both;
  }
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateY(1rem);
  }

  100% {
    opacity: 1;
    transform: translateY(0rem);
  }

  0% {
    opacity: 0;
    transform: translateY(1rem);
  }
}

@-webkit-keyframes slideIn {
  0% {
    -webkit-transform: transform;
    -webkit-opacity: 0;
  }

  100% {
    -webkit-transform: translateY(0);
    -webkit-opacity: 1;
  }

  0% {
    -webkit-transform: translateY(1rem);
    -webkit-opacity: 0;
  }
}

.slideIn {
  -webkit-animation-name: slideIn;
  animation-name: slideIn;
}

.article {
  p.lead {
    &:first-child {
      margin-top: $spacer * 3;
      margin-bottom: $spacer * 2;
    }
  }

  p+h4 {
    margin-top: $spacer * 2;
  }

  p {
    &:not(:last-child) {
      margin-bottom: $spacer * 1.5;
    }
  }

  blockquote {
    margin: $spacer * 2 0;
  }

  p+h5 {
    margin-top: $spacer * 1.5;
  }

  ul {
    padding-left: $spacer;

    >li {
      margin-bottom: $spacer/2;
    }
  }
}

article {
  ol {
    >li {
      &:not(:last-child) {
        margin-bottom: $spacer * 2;
      }
    }
  }
}

figure {
  margin: $spacer * 2 0;
  text-align: center;
}

figcaption {
  margin-top: $spacer/2;
  color: $text-muted;
  font-weight: $font-weight-bold;
  font-size: $font-size-sm;
}

blockquote {
  position: relative;
  padding: $spacer * 2;
  border-radius: $border-radius * 2;

  .btn {
    position: absolute;
    right: $spacer/2;
    bottom: $spacer/2;
  }
}

.comments {
  padding-left: 0;
  list-style: none;

  .comment {
    &:not(:last-child) {
      margin-bottom: $spacer;
    }

    .comments {
      margin-top: $spacer;
    }

    .comment {
      padding-left: $spacer * 1.5;
      border-left: 4px solid $border-color;
